  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Parse the date as UTC
    const postDate = new Date(dateString);
    if (isNaN(postDate.getTime())) {
      console.error("Invalid date:", dateString);
      return "Invalid date";
    }

    // Adjust the postDate by subtracting 1 hour (3600000ms)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Get the current time in UTC
    const now = new Date();

    // Calculate time difference
    const timeDiff = now.getTime() - adjustedPostDate.getTime();
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    const weeks = Math.floor(days / 7);
    const months = Math.floor(days / 30);
    const years = Math.floor(days / 365);

    // Generate formatted output with "ago"
    if (seconds < 60)
      return seconds === 1 ? "1 second ago" : `${seconds} seconds ago`;
    if (minutes < 60)
      return minutes === 1 ? "1 minute ago" : `${minutes} minutes ago`;
    if (hours < 24) return hours === 1 ? "1 hour ago" : `${hours} hours ago`;
    if (days === 1) return "Yesterday";
    if (days < 7) return `${days} days ago`;
    if (weeks < 4) return weeks === 1 ? "1 week ago" : `${weeks} weeks ago`;
    if (months < 12)
      return months === 1 ? "1 month ago" : `${months} months ago`;
    return years === 1 ? "1 year ago" : `${years} years ago`;
  };
  const formatTimeWithDate = (dateString) => {
    if (!dateString) return null;

    // Create a Date object from the provided date string
    const postDate = new Date(dateString);

    // Ensure it's a valid date
    if (isNaN(postDate)) return "Invalid Date";

    // Adjust time by subtracting one hour (3600000 milliseconds)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Extract date components
    const day = String(adjustedPostDate.getDate()).padStart(2, "0");
    const month = String(adjustedPostDate.getMonth() + 1).padStart(2, "0"); // Months are 0-based
    const year = adjustedPostDate.getFullYear();

    // Extract time components
    const hours = String(adjustedPostDate.getHours()).padStart(2, "0");
    const minutes = String(adjustedPostDate.getMinutes()).padStart(2, "0");
    const seconds = String(adjustedPostDate.getSeconds()).padStart(2, "0");

    // Format as "DD-MM-YYYY HH:MM:SS"
    return `${day}-${month}-${year} ${hours}:${minutes}:${seconds}`;
  };

